import React, { Component } from 'react';

export default class About extends Component {
    constructor() {
        super();
        this.state = {
            scrolled : false,
          
            };
        }
    componentDidMount() {
        window.addEventListener('scroll',() => {
            const isTop = window.scrollY < 540;
            if(isTop !== true){
                this.setState({scrolledf : true});
            } 
            else
            {
                this.setState({scrolledf : false});
            }
        });
    }
        componentWillUnmount() {
            window.removeEventListener('scroll');
        }
    
    render() {
        return (
            <div id="About">
                <div className={this.state.scrolledf ? 'About-Hero-Img ' :'About-Hero-Img ani-about'}></div>
                <div className="About-data">
                <h1 className={this.state.scrolledf ? 'about-head ani' :'about-head'}>About Me</h1>
                <h3 className={this.state.scrolledf ? 'About-text-main ania' :'About-text-main'}>I Am  <span style={{'color': 'red' }}>Giridhar Krishna Kolavasi</span>, and my portfolio is a representation of all that i've learned and accomplished as a Undergraduate Student. I love designing Websites and am looking for more opportunities to work and learn new coding.I done inteship in IdealabsFtv as an Blockchain Developer....ðŸ˜Ž</h3>
                </div>
            </div>
        )
    }
}
